export default {
	data() {
		return {
			shrink:[],
			ListLen:0,
		}
	},
	onLoad:function(){
		var _self = this
		_self.getInfo()
	},
	methods: {
		getInfo:function(){
			var _self = this
			uni.request({
				url:_self.$api+"dockingManager/releaseMainQuery",
				data:{id:"0",pull:3,optionId:uni.getStorageSync("openId")},
				method:"GET",
				success:function(res){
					console.log(res)
					var data = res.data
					_self.shrink = res.data 
					_self.ListLen = _self.shrink.length
				}
			})
		},
		goto:function(id){
			uni.navigateTo({
				url:"../parkAdvice/parkAdvice?id="+id
			})
		},
	}
}
